.login {
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login__logo {
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
  }

.login__container {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login__container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
  }

.login__container > form > h5 {
    margin-bottom: 5px;
}

.login__container > form > input {
    height: 30px;
    margin-bottom: 10px;
    background-color: rgb(255, 255, 255);
    width: 98%;
   border: 1px solid lightgray;
box-shadow: -3px 17px 10px -20px rgba(0,0,0,0.45) inset;
  }

.login__container > p {
    margin-top: 15px;
    font-size: 12px;
}

.login__signInButton {
    background:#ffd710d4 ;
    border-radius: 8px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border: none;
   
  }

  .login__registerButton {
    border-radius: 8px;
     width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border: 1px solid lightgray;
    background: white;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
    
  }
  .login__registerButton:hover{
    background: rgb(243, 243, 243);
  }
  .login__signInButton:hover{
    background: #FFD810;
  }
  