.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  width: 100%;
  max-height: 400px;
  min-width: 100px;
  background: white;
  z-index: 1;
}
.product:hover {
box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;}
.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}
.product__price {
  margin-top: 5px;
}
.product__info {
  width: 100%;
  height: 100px;
  margin-bottom: 15px;
}
.product > button {
  background: #ffd814;
  border: none;
  margin-top: 10px;
  border-radius: 10px;
  color: #111;
  padding: 10px;

}
.product > button:hover {
     background: #ebc504;
}
.product__rating {
  display: flex;
}
.product__star {
  color: #ffd500;
}
